@import url("https://fonts.googleapis.com/css2?family=Fraunces:opsz,wght@9..144,700&family=Montserrat:wght@500;700&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-size: 14px;
  font-family: "Montserrat", sans-serif;
}

.body-wrapper {
  background-color: hsl(30deg, 38%, 92%);
  width: 100%;
  max-width: 1440px;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.body-wrapper .card {
  margin: 0 auto;
  width: 60%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  border-radius: 20px;
  background-color: hsl(0deg, 0%, 100%);
}
@media screen and (max-width: 425px) {
  .body-wrapper .card {
    height: 60vh;
  }
}
.body-wrapper .card .card__img {
  width: 50%;
  height: 100%;
  background-image: url("/images/image-product-desktop.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
}
@media screen and (max-width: 425px) {
  .body-wrapper .card .card__img {
    height: 100%;
  }
}
.body-wrapper .card .card__content {
  width: 50%;
  height: 100%;
  padding: 2rem 3rem;
}
@media screen and (max-width: 768px) {
  .body-wrapper .card .card__content {
    width: 60%;
    padding: 0.8rem 0.9rem;
    height: 50%;
  }
}
.body-wrapper .card .card__content .content__subtitle {
  text-transform: uppercase;
  color: hsl(212deg, 21%, 14%);
  font-weight: lighter;
  font-size: 1rem;
  padding-bottom: 2rem;
}
@media screen and (max-width: 1115px) {
  .body-wrapper .card .card__content .content__subtitle {
    padding-bottom: 1rem;
  }
}
@media screen and (max-width: 768px) {
  .body-wrapper .card .card__content .content__subtitle {
    font-size: 0.6rem;
  }
}
@media screen and (max-width: 425px) {
  .body-wrapper .card .card__content .content__subtitle {
    font-size: 0.2rem;
  }
}
.body-wrapper .card .card__content .content__title {
  font-family: "Fraunces", serif;
  font-size: 2.5rem;
  padding-bottom: 2rem;
}
@media screen and (max-width: 1115px) {
  .body-wrapper .card .card__content .content__title {
    padding-bottom: 1rem;
    font-size: 2rem;
  }
}
@media screen and (max-width: 768px) {
  .body-wrapper .card .card__content .content__title {
    font-size: 1.2rem;
  }
}
@media screen and (max-width: 425px) {
  .body-wrapper .card .card__content .content__title {
    font-size: 0.6rem;
  }
}
.body-wrapper .card .card__content .content___about {
  width: 100%;
  color: hsl(228deg, 12%, 48%);
  font-size: 1.1rem;
  text-align: left;
  padding-bottom: 2rem;
}
@media screen and (max-width: 1115px) {
  .body-wrapper .card .card__content .content___about {
    padding-bottom: 1rem;
    font-size: 0.9rem;
  }
}
@media screen and (max-width: 768px) {
  .body-wrapper .card .card__content .content___about {
    font-size: 0.7rem;
  }
}
@media screen and (max-width: 425px) {
  .body-wrapper .card .card__content .content___about {
    font-size: 0.3rem;
  }
}
.body-wrapper .card .card__content .content__price {
  padding-bottom: 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 70%;
}
@media screen and (max-width: 1115px) {
  .body-wrapper .card .card__content .content__price {
    width: 90%;
  }
}
@media screen and (max-width: 1032px) {
  .body-wrapper .card .card__content .content__price {
    width: 100%;
  }
}
.body-wrapper .card .card__content .content__price .price {
  font-family: "Fraunces", serif;
  color: hsl(158deg, 36%, 37%);
  font-weight: bold;
  font-size: 2.5rem;
}
@media screen and (max-width: 1115px) {
  .body-wrapper .card .card__content .content__price .price {
    font-size: 2rem;
  }
}
@media screen and (max-width: 948px) {
  .body-wrapper .card .card__content .content__price .price {
    font-size: 1.5rem;
  }
}
@media screen and (max-width: 808px) {
  .body-wrapper .card .card__content .content__price .price {
    font-size: 1.3rem;
  }
}
@media screen and (max-width: 768px) {
  .body-wrapper .card .card__content .content__price .price {
    font-size: 1.2rem;
  }
}
@media screen and (max-width: 425px) {
  .body-wrapper .card .card__content .content__price .price {
    font-size: 0.5rem;
  }
}
.body-wrapper .card .card__content .content__price .old-price {
  text-decoration: line-through;
  color: hsl(228deg, 12%, 48%);
}
@media screen and (max-width: 948px) {
  .body-wrapper .card .card__content .content__price .old-price {
    font-size: 0.8rem;
  }
}
@media screen and (max-width: 768px) {
  .body-wrapper .card .card__content .content__price .old-price {
    font-size: 0.6rem;
  }
}
@media screen and (max-width: 425px) {
  .body-wrapper .card .card__content .content__price .old-price {
    font-size: 0.2rem;
  }
}
.body-wrapper .card .card__content .add-to-card {
  width: 100%;
  padding: 1rem;
  border-radius: 10px;
  border: none;
  background-color: hsl(158deg, 36%, 37%);
  color: hsl(0deg, 0%, 100%);
  font-size: 1rem;
  font-weight: 600;
}
.body-wrapper .card .card__content .add-to-card img {
  padding-right: 1rem;
}
@media screen and (max-width: 768px) {
  .body-wrapper .card .card__content .add-to-card img {
    padding-right: 0.5rem;
  }
}
@media screen and (max-width: 425px) {
  .body-wrapper .card .card__content .add-to-card img {
    width: 0.8rem;
  }
}
@media screen and (max-width: 768px) {
  .body-wrapper .card .card__content .add-to-card {
    font-size: 0.9rem;
  }
}
@media screen and (max-width: 838px) {
  .body-wrapper .card .card__content .add-to-card {
    font-size: 0.8rem;
  }
}
@media screen and (max-width: 425px) {
  .body-wrapper .card .card__content .add-to-card {
    font-size: 0.3rem;
    padding: 0.5rem;
  }
}